import React, { useState, useEffect } from 'react';
import { useParams } from 'react-router-dom';
import ReactMarkdown from 'react-markdown';
import remarkGfm from 'remark-gfm';
import yaml from 'js-yaml';

import { Button } from 'primereact/button';

import './Stories.css';

const markdown_file = require('../assets/stories/story1.md');

const fetchStories = async (): Promise<any[]> => {
  const storyPaths = [markdown_file];
  const stories = await Promise.all(
    storyPaths.map(async (filepath) => {
      const response = await fetch(filepath);
      if (!response.ok) {
        console.error(`Failed to fetch ${filepath}: ${response.statusText}`);
        return null;
      }
      const fileContents = await response.text();

      const contentSplit = fileContents.split('---');
      const yamlContent = contentSplit[1]?.trim();
      const content = contentSplit.slice(2).join('---').trim();

      const data = yaml.load(yamlContent);

      const fileName = filepath.split('/').pop() || ''; // Extract file name from path

      return {
        data,
        content,
        slug: fileName.replace('.md', ''), // Extract slug from file name
      };
    })
  );

  return stories.filter(story => story !== null); // Filter out any null values
};

const Stories: React.FC = () => {
  const [stories, setStories] = useState<any[]>([]);
  const [currentStory, setCurrentStory] = useState<any | null>(null);
  const { slug } = useParams<{ slug: string }>();

  useEffect(() => {
    const fetchAllStories = async () => {
      const allStories = await fetchStories();
      setStories(allStories);
    };
    fetchAllStories();
  }, []);

  useEffect(() => {
    if (slug) {
      const story = stories.find((story) => story.slug === slug);
      setCurrentStory(story);
    }
  }, [slug, stories]);

  if (currentStory) {
    return (
      <div className="story-content-card">
        <div className="back-button">
          <Button 
            icon="pi pi-arrow-left" 
            label="Back" 
            className="p-button-text" 
            onClick={() => setCurrentStory(null)} 
          />
        </div>
        <ReactMarkdown remarkPlugins={[remarkGfm]}>
          {currentStory.content}
        </ReactMarkdown>
      </div>
    );
  }

  return (
    <div className='stories-list-container'>
      <h1>Stories</h1>
      <div className="stories-list">
        {stories.map((story) => (
          <div key={story.data.slug} className="story-card">
            {/* eslint-disable-next-line jsx-a11y/anchor-is-valid */}
            <a onClick={() => setCurrentStory(story)} className="story-link">
              <h4>{story.data.Date.toLocaleDateString('en-CA')}</h4>
              <img src={story.data.Thumbnail} alt={story.data.Title} />
              <h3>{story.data.Title}</h3>
            </a>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Stories;